body {
  background-color: #0b0527;
}

.AIOutput {
  text-align: left;
  color: #d9d9d9;
  font-family: Arial, sans-serif; /* Using Roboto font from Google Fonts */
  font-size: 2rem; /* A comfortable font size for reading */
  line-height: 5rem; /* Spacing between lines for readability */
  letter-spacing: .1rem; /* Slight spacing between letters */
  max-width: 80%; /* Limiting the width for long paragraphs */
  margin: 20px auto; /* Top-bottom margin and auto left-right for centering */
  padding: 20px; /* Some padding around the text */
  border-radius: 5px; /* Optional: Rounded corners for aesthetic */
}

h1 {
  margin: 200px 0 0 0;
  padding: 0 0 80px 0;
  font-size: 3rem;
  text-align: center;
  color: white;
}

