.Bar {
  width: 50%;     /* Adjust as needed */
  padding: 15px;   /* Adjust for bigger padding */
  font-size: 1.8rem; /* Adjust for bigger font size */
  border: 1px solid #757575;
  border-radius: 5px; /* Optional: for rounded corners */
  background-color: #757575;
}

